body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.hide{
  display: none;
}
body{ 
  background: #f6f6f6;
}

.dateEdgeBlock{
  margin-top: 30px;
  margin-bottom: 30px;
  text-align: center;
}
.dateEdgeBlock .dateEdgeElem{
  display: inline-block;
  margin-right: 30px;
  position: relative;
  width: 40%;
  
}
.dateEdgeBlock .dateEdgeElem .show{
  position: absolute;
  z-index:100;
}
.dateEdgeBlock .dateEdgeElem:last-child{
  margin-right: 0;
}
.dateEdgeBlock .dateEdgeElem .dateValue{
  width: 100%;
  height: 40px;
  cursor: pointer;
  border: 1px solid #e8e8e8;
  overflow: hidden;
  border-radius: 6px;
  background: #fff;
  color: #71666e;
  display: flex;
  align-items: center;
  justify-content: center;
}
.Button{
    border: 1px solid #bcbcbc;
    box-shadow: 0 6px #bcbcbc;
    height: 60px;
    color: #bfc0c2;
    text-transform: uppercase;
    border-radius: 6px;
    background: none;
    margin-right: 20px;
    cursor: pointer;
}
.Button:active{
    outline: none;
    box-shadow: 0 4px #bcbcbc;
    -webkit-transform: translateY(3px);
            transform: translateY(3px);
}
.Button:focus{
    outline: none;
}
.prevDay{
    width: 80px;
    height: 60px;
    background: url(data:image/jpeg;base64,) no-repeat center center;
    opacity: 0.3;
    cursor: unset;
    box-shadow: none;
}
.prevDay:active,
.nextDay:active{
    box-shadow: none;
    -webkit-transform: unset;
            transform: unset;
}
.nextDay{
    width: 80px;
    height: 60px;
    background: url(data:image/jpeg;base64,) no-repeat center center;
    opacity: 0.3;
    cursor: unset;
    box-shadow: none;
}
.active{
    opacity: 1;
    cursor: pointer;
    box-shadow: 0 6px #bcbcbc;
}
.active:active{
    outline: none;
    box-shadow: 0 4px #bcbcbc;
    -webkit-transform: translateY(3px);
            transform: translateY(3px);
}
.today{
    width: 126px;
    font-size: 20px;
}
.nowToday{
    box-shadow: none;
    cursor: unset;
    opacity: 0.3;
}
.CurrentDate{
    display: inline-block;
    height: 60px;
    width: 600px;
    position: relative;
    margin-right: 20px;
}
.CurrentDate .show{
    position: absolute;
    z-index: 100;
}
.CurrentDate .currentDateValue{
    background: #fff;
    border: 1px solid #e7e7e7;
    border-radius: 6px;
    width: 100%;
    height: 100%;
    color: #62666f;
    font-size: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    padding-left: 5px;
    padding-right: 5px;
}
.CurrentDate .nowToday{
    color: blue;
    border-color: blue;
    box-shadow: 1px 0px 26px blue;
    opacity: 1;
}
.DaysPager{
    width: 90%;
    max-width: 700px;
    margin: 0 auto;
    height: 60px;
    display: flex;
}
.timeZone{
    height: 100%;
    display:flex;
    font-size: 20px;
    align-items: center;
    justify-content: center;
}
